import { ReactNode, createContext, useContext, useState } from "react"
import { lineLayer, pointLayer, polygonLayer } from "./core/appConfig";
export type AppContextActions = {
    dataSetsNames: string[],
    sideBar?: boolean,
    message?: { type: 'loading', content: string },
    layersDemo?: 'dynamic' | 'static',
    layers: any[],
    index: number
};
export type AppContextState = {
    actions: AppContextActions
    setActions: (newActions: AppContextActions) => void
}
export const AppContext = createContext<AppContextState>({
    actions: { dataSetsNames: [], sideBar: false, layersDemo: 'dynamic', layers: [], index: 1 },
    setActions: () => { },
})
export const useAppContext = () => useContext(AppContext)


export function AppContextProvider({ children }: { children: ReactNode }) {
    const [actions, setActions] = useState<AppContextActions>({
        index: 1,
        dataSetsNames: [], sideBar: false, layersDemo: 'dynamic', layers:
            [{ name: 'Plots shape', id: '1', show: true, style: polygonLayer({ source: 'Plots shape' }) },
            { name: 'Lines tracks', id: '4', show: true, style: lineLayer({ source: 'Lines tracks' }) },
            { name: 'Light street', id: '2', show: true, style: pointLayer({ iconImage: 'poi_shootingrange_7_i', caption: true, source: 'Lights street', text: "coverage", fields: ["syscode", "max_speed", "coverage", "name"] }) },
            { name: 'Waste frames', id: '3', show: true, style: pointLayer({ iconImage: 'poi_trashbin_1_i', caption: true, source: 'Waste frames', text: "frameid", fields: ["frameid"] }) },]
    });
    return (
        <AppContext.Provider value={{ actions, setActions }}>
            {children}
        </AppContext.Provider>
    );
}