import { Button, Input, Select } from "antd";
import React, { useEffect, useState } from "react";
import { useAppContext } from "../../AppContext";
import { lineLayer, pointLayer, polygonLayer } from "../../core/appConfig";


const NewLayerCard: React.FC<{ onFinish: () => void }> = (props: any) => {
    const { actions, setActions } = useAppContext();
    const [typeLayer, setTypeLayer] = useState('point');
    const [layerName, setNewLayerName] = useState('New layer ' + (Math.max(...actions.layers.map(l => +l.id), 0) + 1))
    const handleChange = (value: string) => {
        setTypeLayer(value)
    };

    const setName = (name: string) => {
        setNewLayerName(name)
    }

    const addLayer = () => {
        if (typeLayer === 'polygon') {
            const layer = { name: layerName, id: `${Math.max(...actions.layers.map(l => +l.id), 0) + 1}`, show: true, style: polygonLayer({}) };
            setActions({ ...actions, layers: [layer, ...actions.layers] });
        }
        if (typeLayer === 'line') {
            const layer = { name: layerName, id: `${Math.max(...actions.layers.map(l => +l.id), 0) + 1}`, show: true, style: lineLayer({}) };
            setActions({ ...actions, layers: [layer, ...actions.layers] });
        }
        if (typeLayer === 'point') {
            const layer = { name: layerName, id: `${Math.max(...actions.layers.map(l => +l.id), 0) + 1}`, show: true, style: pointLayer({ iconImage: 'ent', caption: false, source: '', text: "", fields: ["frameid", "syscode", "max_speed", "coverage", "name", ""] }) };
            setActions({ ...actions, layers: [layer, ...actions.layers] });
        }
        props.onFinish();
    }

    useEffect(() => {
        setNewLayerName('New layer ' + (Math.max(...actions.layers.map(l => +l.id),0) + 1));
    }, [actions.layers.length])

    return (
        <div className="layersDiv">
            <span>Layer type: <span className="titleLayer">    <Select
                value={typeLayer}
                style={{ width: '50%' }}
                onChange={handleChange}
                options={[
                    { value: 'line', label: 'Line' },
                    { value: 'polygon', label: 'Polygon' },
                    { value: 'point', label: 'Point' },
                ]}
            /></span></span>
            <p />
            <span>Layer name: <span className="titleLayer"><Input onInput={e => setName((e.target as any).value)} style={{ width: '48%' }} value={layerName} /></span></span>
            <p />
            <Button onClick={addLayer} style={{ width: '100%' }}>Add layer</Button>
        </div>
    );
};

export default NewLayerCard;