import React, { forwardRef } from "react";
import { Collapse } from 'antd';
import { CollapseProps } from "antd/lib/collapse/Collapse";
import { CopyOutlined, DeleteOutlined, EyeInvisibleOutlined, EyeOutlined } from "@ant-design/icons";
import { useAppContext } from "../../AppContext";
import LayerCard from "./layerCard";


const Layer = forwardRef<HTMLLIElement, any>(
    ({ id, name, show, dragHandleProps, snapshot, ...props }, ref) => {
        const { actions, setActions } = useAppContext();


        const toggleShow = (show: boolean) => {
            const layers = actions.layers;
            layers.forEach((layer: any) => {
                if (layer.id === id) {
                    layer.show = show;
                }
            })
            setActions({ ...actions, layers, index: actions.index + 1 })
        }


        const removeLayer = () => {
            const newLayers = actions.layers.filter(layer => layer.id !== id);
            const datasets = newLayers.map(l => l.style.sourceId)
            setActions({ ...actions, layers: newLayers, dataSetsNames: datasets.filter((d: any) => d !== '' ? true : false) })
        }

        const genExtra = () => (<div className="extraLayer">
            <DeleteOutlined onClick={(event) => {
                removeLayer()
                event.stopPropagation();
            }} />

            {show ?
                <EyeOutlined
                    onClick={(event) => {
                        toggleShow(false)
                        event.stopPropagation();
                    }}
                /> : <EyeInvisibleOutlined
                    onClick={(event) => {
                        toggleShow(true)
                        event.stopPropagation();
                    }}
                />}

        </div>
        );

        const items: CollapseProps['items'] = [
            {
                key: id,
                label: name,
                children: <LayerCard id={id} />,
                extra: genExtra(),
            },
        ];

        const onChange = (key: string | string[]) => {
            
        };

        return (
            <li
                ref={ref}
                {...props}
                className={
                    "comment card p-2 my-2 " + (snapshot.isDragging ? "hovering" : "")
                }
            >
                <span {...dragHandleProps}>
                    <Collapse onChange={onChange} items={items} />
                    <br></br>
                </span>
            </li>
        );
    },
);

export default Layer;
