import { memo, useEffect } from 'react';
import { load } from '@2gis/mapgl';
import { useMapglContext } from './mapglContext';
import { useAppContext } from '../../AppContext';
import { dataSetsNames } from '../../core/appConfig';


const MapWrapper = memo(
    function MapContainer() {
        return <div id='map-container' style={{ width: '100%', height: '100%' }}></div>;
    },
    () => true,
);

export default function Mapgl() {
    const { setMapglContext } = useMapglContext();
    const { actions, setActions } = useAppContext();

    useEffect(() => {
        let map: mapgl.Map | undefined = undefined;

        load().then((mapgl) => {
            map = new mapgl.Map('map-container', {
                center: [54.55937, 24.43],
                zoom: 14,
                key: 'a1893935-6834-4445-b97a-3405fb426c5b',
                style: 'aab82e48-f3ae-4bc8-aa1a-17bb9139128c',
                maxZoom: 22
            });

            setActions({ ...actions, dataSetsNames })

            setMapglContext({
                mapglInstance: map,
                mapgl,
            });

            let popup = new mapgl.HtmlMarker(map, {
                coordinates: [],
                html: '',
            });

            map.on('click', (e) => {
                if (map && (e.targetData as any)?.attributes?.category) {
                    popup.destroy();
                    popup = new mapgl.HtmlMarker(map, {
                        coordinates: e.lngLat,
                        html: `<div class="popup">
                        <div class="popup-content">
                            <p>Uniqueid:<span>${(e.targetData as any)?.attributes?.uniqueid}</span></p>
                            <p>Category:<span>${(e.targetData as any)?.attributes?.category}</span></p>
                        </div>
                        <div class="popup-close">x</div>
                        <div class="popup-tip"></div>
                    </div>`,
                    });
                    const popupHtml = popup.getContent();
                    (popupHtml as any).querySelector('.popup-close').addEventListener('click', () => {
                        popup.getContent().style.display = 'none';
                    });
                    popup.getContent().style.display = 'block';
                } else {
                    popup.getContent().style.display = 'none';
                }

            })
        });

        // Destroy the map, if Map component is going to be unmounted
        return () => {
            map && map.destroy();
            setMapglContext({ mapglInstance: undefined, mapgl: undefined });
        };
    }, [setMapglContext]);

    return <MapWrapper />;
}