
import { AppstoreAddOutlined, SisternodeOutlined } from '@ant-design/icons';
import { Collapse, CollapseProps, Divider, Select } from 'antd';
import React, { useEffect, useState } from 'react';
import { dataSetsNames, style } from '../../core/appConfig';
import { useAppContext } from '../../AppContext';
import { DragDropContext, Draggable, Droppable } from "react-beautiful-dnd";
import LayersList from './layersList';
import Layer from './layer';
import NewLayerCard from './newLayerCard';

function DynamicLayersPanel() {

    const { actions, setActions } = useAppContext();
    const [active, setActive] = useState('');
    const setFinish = () => {
        setActive('')
    }

    const items: CollapseProps['items'] = [
        {
            key: 'addnew',
            label: 'Add new layer',
            children: <NewLayerCard onFinish={setFinish} />,

        },
    ];

    const filteredOptions = dataSetsNames.filter((o) => !actions.dataSetsNames.includes(o));

    const setSelectedItems = (items: any) => setActions({ ...actions, dataSetsNames: items });

    const dragEnded = (param: any) => {
        const { source, destination } = param;
        let _arr = [...actions.layers];
        //extracting the source item from the list
        const _item = _arr.splice(source.index, 1)[0];
        //inserting it at the destination index.
        if (destination) {
            _arr.splice(destination.index, 0, _item);
            // console.log(actions.layers,_arr)
            setActions({ ...actions, layers: _arr, index: actions.index + 1 });
        }
    };

    return (
        <div style={{ padding: '6px' }}>
            <div style={{ fontSize: '16px', color: 'black' }}><SisternodeOutlined style={{ fontSize: '24px', color: 'rgb(74, 21, 232)' }} /> Active data sources</div>
            <Select
                mode="multiple"
                disabled={true}
                placeholder="Inserted are removed datasets"
                value={actions.dataSetsNames}
                onChange={setSelectedItems}
                style={{ width: '100%' }}
                options={filteredOptions.map((item) => ({
                    value: item,
                    label: item,
                }))}
            />
            <Divider />

            <Collapse activeKey={active} onChange={() => setActive(active == 'addnew' ? '' : 'addnew')} style={{ width: 315 }} items={items} expandIcon={() => <AppstoreAddOutlined />} />

            <DragDropContext onDragEnd={dragEnded}>
                <Droppable droppableId="layers-wrapper">
                    {(provided: any, snapshot: any) => (
                        <LayersList ref={provided.innerRef} {...provided.droppableProps}>
                            {actions.layers.map((_layer, index) => {
                                return (
                                    <Draggable
                                        draggableId={`layer-${_layer.id}`}
                                        index={index}
                                        key={_layer.id}
                                    >
                                        {(_provided: any, _snapshot: any) => (
                                            <Layer
                                                ref={_provided.innerRef}
                                                dragHandleProps={_provided.dragHandleProps}
                                                {..._provided.draggableProps}
                                                snapshot={_snapshot}
                                                {...{ id: _layer.id, name: _layer.name, show: _layer.show }}
                                            />
                                        )}
                                    </Draggable>
                                );
                            })}
                            {provided.placeholder}
                        </LayersList>
                    )}
                </Droppable>
            </DragDropContext>


        </div>
    );
}

export default DynamicLayersPanel;