
import { SisternodeOutlined, SnippetsOutlined } from '@ant-design/icons';
import { Button, Divider, Input, Select, Steps } from 'antd';
import React, { useEffect, useState } from 'react';
import { dataSetsNames, style } from '../../core/appConfig';
import { useAppContext } from '../../AppContext';
import { useMapglContext } from '../mapgl/mapglContext';
import Link from 'antd/es/typography/Link';

function StaticLayersPanel() {
    const { mapglInstance } = useMapglContext();
    const { actions, setActions } = useAppContext();
    const [currStep, setCurrStep] = useState(0);
    const [styleId, setStyleId] = useState('');
    const step1now = () => setCurrStep(1);
    const step2now = (e: any) => { setStyleId(e.target.value); setCurrStep(2) };
    const step3now = () => { mapglInstance?.setStyleById(styleId || style); setCurrStep(0) };

    const filteredOptions = dataSetsNames.filter((o) => !actions.dataSetsNames.includes(o));

    const setSelectedItems = (items: any) => setActions({ ...actions, dataSetsNames: items });

    return (
        <div style={{ padding: '6px' }}>
            <div style={{ fontSize: '16px', color: 'black' }}><SisternodeOutlined style={{ fontSize: '24px', color: 'rgb(74, 21, 232)' }} /> Active data sources</div>
            <Select
                mode="multiple"
                placeholder="Inserted are removed datasets"
                value={actions.dataSetsNames}
                onChange={setSelectedItems}
                style={{ width: '100%' }}
                options={filteredOptions.map((item) => ({
                    value: item,
                    label: item,
                }))}
            />
            <Divider />
            <p />

            <div style={{ fontSize: '16px', color: 'black' }}><SnippetsOutlined style={{ fontSize: '24px', color: 'rgb(74, 21, 232)' }} /> In style layers</div>
            <p>This layers styled in map custom style</p>
            <p>You can edit layers by <a target="_blank" rel="noreferrer" href='https://docs.2gis.com/en/mapgl/styles/overview/editor'>Map styles editor</a></p>
            <br />
            <Steps current={currStep}
                direction="vertical"
                items={[
                    {
                        title: 'Open editor in style with custom datasets layers',
                        description: <div>
                            <a target="_blank" rel="noreferrer" href="https://styles.2gis.com/preview/34415e89-88f8-44bb-986e-c96cd0dcfd96?allowCopy=-995525160" ><Button style={{ width: 240 }} onClick={step2now}>Edit custom styles now</Button></a>
                            <p />
                            <a target="_blank" rel="noreferrer" href="https://styles.2gis.com"><Link style={{ width: 240, textAlign: 'center', display: 'block' }} onClick={step1now}>Just, open style editor</Link></a>
                        </div>
                    },
                    {
                        title: 'Insert you custom style Id',
                        description: <Input onChange={step2now} size="large" placeholder="34415e89-88f8-44bb-986e-c96cd0dcfd96" />,
                    },
                    {
                        title: 'Apply/reload style',
                        description: <Button style={{ width: 240 }} onClick={step3now}>Apply style</Button>,
                    },
                ]}
            />
        </div>
    );
}

export default StaticLayersPanel;