

import { useEffect } from 'react';
import { useAppContext } from '../AppContext';
import { useMapglContext } from '../ui/mapgl/mapglContext';
import { dataSets, dataSetsMetatiles } from './appConfig';


function ConnectorDataSources() {
    const { actions, setActions } = useAppContext();
    const { mapgl, mapglInstance } = useMapglContext();
    useEffect(() => {
        if (!mapglInstance || !mapgl) { setActions({ ...actions, dataSetsNames: [] }); return }
        const sources = actions.dataSetsNames.map(name => {
            return new (mapgl as any)._J.ZenithSource((mapglInstance as any)._impl, {
                tileTemplateUrl: dataSets[name],
                metatileTemplateUrl: dataSetsMetatiles[name],
                tileServer: '',

                tileSet: '',
                tileProtocol: 'http',
                tileKey: '',
                appId: (mapglInstance as any)._impl.state.appId,
                subdomains: [''],
                sourceAttributes: {
                    sourceName: dataSets[name],
                    sourceId: name,
                    sourceType: 'ALD_TileSource'
                },
                disableTilesAnimation: true,
                identifyAsDefaultSource:true,
                promoteAttributes: ['uniqueid','category'],
            });
        });

        return () => {
            sources.forEach(source => source.destroy());
        }
    }, [actions.dataSetsNames])

    return <></>
}

export default ConnectorDataSources;



