import React, { useEffect } from 'react';
import { ConfigProvider, Layout } from 'antd';
import { useAppContext } from './AppContext';
import Mapgl from './ui/mapgl/mapgl';
import { useMapglContext } from './ui/mapgl/mapglContext';
import Message from './ui/notification';
import SiderPanel from './ui/sider';
import { useWindowSize } from './hooks/useWindowResize';
import ConnectorDataSources from './core/connectorDataSources';


const { Content } = Layout;

const layoutStyle = {
  height: '100vh',
};



function App() {

  const { actions, setActions } = useAppContext();
  const { mapglInstance } = useMapglContext();
  const [width, height] = useWindowSize();
  setTimeout(() => { if (actions.index == 1) { setActions({ ...actions, index: actions.index + 1 }) } },1500)
  useEffect(() => { setTimeout(() => mapglInstance?.invalidateSize(), 100); setTimeout(() => mapglInstance?.invalidateSize(), 200) }, [width, height, mapglInstance, actions]);
  return (
    <ConfigProvider
      theme={{
        components: {
          Tabs: {
            inkBarColor: 'rgb(74, 21, 232);',
            itemHoverColor: 'rgb(74, 21, 232);',
            itemActiveColor: 'rgb(74, 21, 232);',
            itemSelectedColor: 'rgb(74, 21, 232);',
          },
          Button: {
            dangerShadow: 'silver',
            dangerColor: 'gray'
          },
        },
        token: {
          colorPrimaryHover: 'rgb(74, 21, 232);',
          colorPrimary: 'rgb(74, 21, 232);',
        },
      }}
    >

      <Layout style={layoutStyle} hasSider>
        <SiderPanel />
        <Layout>
          <Content>
            <Mapgl />
            <ConnectorDataSources />
          </Content>
        </Layout>
        <Message />
      </Layout>
    </ConfigProvider>
  );
}

export default App;
