export type PointProps = { iconImage?: string, caption?: boolean, text?: string, fields?: string[], source?: string };
export type BaseProps = { source?: string };

export const pointLayer = (props: PointProps) => {
    return {
        type: "point",
        iconImage: props.iconImage || "ent",
        textFont: props.caption ? 'Noto_Sans_Semibold' : '',
        iconWidth: 16,
        textColor: '#cccccc',
        textFontSize: 8,
        textField: props.text || '',
        beforeId: '',
        fields: props.fields || [],
        sourceId: props.source || ''
    }
}

export const polygonLayer = (props: BaseProps) => {
    return {
        type: "polygon",
        color: "rgba(185,94,214,0.33)",
        strokeColor: "#FD8F60",
        strokeWidth: 1,
        heigh: false,
        heightValue: 200,
        beforeId: '',
        sourceId: props.source || ''
    }
}

export const lineLayer = (props: BaseProps) => {
    return {
        type: "line",
        color: "#9605FF",
        width: 2,
        heigh: false,
        heightValue: 200,
        beforeId: '',
        sourceId: props.source || ''
    }
}